<template>    
    <div class="container">
        <div class="row ps-4 pe-4">
        <div class="col-md-12 text-center mb-4">
            <h2 class="text-dark fw-bold burgundy-gradient-text">
                Contacts
            </h2>
            <p class="text-secondary" style="font-size: 15px; letter-spacing: 0.4px;">Reach out to us for any inquiries.</p>
        </div>
            <div class="col-md-4 col-sm-12 mt-4 order-sm-2">
                <h3 class="fw-bold text-dark">
                    Feel free to say hi!
                </h3>

                <div class="d-flex mt-4 ps-2">
                    <p class="mt-0 mb-3"> 
                        <font-awesome-icon class="me-2 bg-burgundy text-white circle-icon" style="font-size: 18px" :icon="['fas', 'location-arrow']"></font-awesome-icon>  
                    </p>
                    <div class="d-block">
                        <p v-for="(text) in prop_data.address.split(',')" :key="text"  class="ms-2 mb-1" style="font-size: 16px">{{ text }}</p>
                    </div>
                </div>

                <div class="d-flex mt-2 ps-2">
                    <p class="mt-0 mb-3"> 
                        <font-awesome-icon class="me-2 bg-burgundy text-white circle-icon" style="font-size: 18px" :icon="['fas', 'phone-alt']"></font-awesome-icon>  
                    </p>
                    <div class="d-block">
                        <p class="ms-2 mt-1 mb-1" style="font-size: 16px">{{ prop_data.phone_number }}</p>
                    </div>
                </div> 

                <div class="d-flex mt-2 ps-2">
                    <p class="mt-0 mb-3"> 
                        <font-awesome-icon class="me-2 bg-burgundy text-white circle-icon" style="font-size: 18px" :icon="['fas', 'envelope']"></font-awesome-icon>  
                    </p>
                    <div class="d-block">
                        <p class="ms-2 mt-1 mb-1" style="font-size: 16px">{{ prop_data.email }}</p>
                    </div>
                </div>                                            
            </div> 

            <form action="#" @submit.prevent="handleSubmit()" class="col-md-8 col-sm-12 mt-4 order-sm-1">
                
                <status v-model:status_data=status_data></status>

                <div class="row mb-4">
                    <label for="first_name" class="fw-bold mb-2">Your names</label>
                    <div class="input-group">
                        <input v-model="form_data.names" id="first_name" type="text" class="form-control" placeholder="Enter your Names" required> 
                    </div>
                </div>

                <div class="row mb-4">
                    <label for="first_name" class="fw-bold mb-2">Your Email</label>
                    <div class="input-group">
                        <input v-model="form_data.email" id="email" type="email" class="form-control" placeholder="Enter your email" required> 
                    </div>
                </div>

                <div class="row mb-4">
                    <label for="subject" class="fw-bold mb-2">Subject</label>
                    <div class="input-group">
                        <input v-model="form_data.subject" id="subject" type="text" class="form-control" placeholder="Enter subject of your email. " required> 
                    </div>
                </div>

                <div class="row mb-4">
                    <label for="first_name" class="fw-bold mb-2">Message</label>
                    <div class="input-group">
                        <textarea v-model="form_data.description" name="message" id="message" class="form-control" rows="3"></textarea>
                    </div>
                </div>

                <div class="row">
                    <button class="btn bg-burgundy text-white mx-auto" style="line-height: 2.5; width: 98%">Submit 
                        <font-awesome-icon class="ms-2" :icon="['fas', 'angle-right']"></font-awesome-icon> 
                    </button>
                </div>
            </form>           
        </div>
    </div>
</template>

<style lang="scss" scoped>
    @import '../../../assets/scss/abstracts/_variables.scss';
    
    .contact-title {
        // font-size: 1rem; 
        margin-top: 1rem;
    }

    .form-select, .form-control {
        line-height: 3;
        border: 1px solid #eee;
    }

    .form-select option{
        color: $dark-color;
    }

    .form-select option:first-child{
        color: $secondary-secondary-color;
    }

    .card {
        border: 1px solid #eee;
    }

    .circle-icon {
        padding: 0.6rem;
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
</style>

<script>
    import Status from '../../Status'

    export default {
        name: 'Contacts',
        props: {
            prop_data: {
                type: Object,
                required: true
            }
        },
        components: {
            Status
        },
        data() {
            return {
                form_data: {
                    names: null,
                    email: null,
                    purpose: null,
                    phone_number: null,
                    description: null,
                    property: null
                },
                status_data: {
                    "success": false,
                    "success_message": 'Form Submission was successful!',
                    "error": false,
                    "error_message": 'There are some errors.',
                    "loading": false
                },                  
            }
        },
        methods: {
            handleSubmit() {
                this.status_data.loading = true

                this.axios.post('api/contact/', this.form_data).then(() => {
                    this.status_data.loading = false
                    this.status_data.success = true
                    this.status_data.error = false
                    this.status_data.success_message = 'Success!'
                }).catch((error) => {
                    this.status_data.loading = false
                    this.status_data.success = false
                    this.status_data.error = true
                    this.status_data.error_message = 'Error! ' + error.response.data.message
                })
            },                   
        },   
    }
</script>